.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item button {
  width: 35px;
  margin: 3px;
}

.todo-list-item-label {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  user-select: none;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
  font-weight: bold;
  color: #ffc000
  ;
}

@media (max-width:600px) {

  .todo-list-item button {
    height: 20px;
    width: 35px;
    margin: 3px;
  }
  
  .todo-list-item-label  {
    margin-left: 0.5rem;
    line-height: 3px;
    cursor: pointer;
    user-select: none;
  }

}
